.notfound {
    background-color: #fafafa;
    padding-top: 32px;
    padding-bottom: 32px;
    height: 100%;

    &__title {
        padding-top: 32px;
        font-size: 56px;
        font-family: Righteous;
        color: #242424;
        text-align: center; }

    &__subtitle {
        font-size: 20px;
